import React from 'react';
import './App.css';
import SearchBar from '../SearchBar/SearchBar';
import Building from '../Building/Building'

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {imageSrc: '',
                  address: '',
                  area: '',
                  floors: '',
                  selectAddress: false};
    this.changeAddress = this.changeAddress.bind(this);
  }

  changeAddress(newAddress,newArea,newImageSrc,newFloors,selectedAddress) {
    this.setState({address: newAddress})
    this.setState({area:newArea})
    this.setState({imageSrc: newImageSrc})
    this.setState({floors: newFloors})
    this.setState({selectAddress: selectedAddress})
  } 

  

  render() {
    return (
      <div className="App">
        <h1>Bygningspas</h1>
        <SearchBar address={this.state.address} onChange={this.changeAddress}/>
        {this.state.selectAddress ? <Building building={this.state}/> : <div></div>}
      </div>
    );
  }
}

export default App;