import React from 'react';
import './Building.css';

class Building extends React.Component {
  render() {
    return (
      <div className="Building">
        <div className="image-container">
          <img src={this.props.building.imageSrc} alt=''/>
        </div>
        <h2>{this.props.building.address}</h2>
        <div className="adress-information">
          <div className="Building-address">
            <p>{this.props.building.area}</p>
            <p>{this.props.building.floors}</p>
          </div>
        </div>
      </div>
    );
  }
}
export default Building;